import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import styles from './workspaceDomainFormLayout.scss';
import { t } from 'sn-translate';
import { FieldSetElement } from '@devsnc/sn-scripted-modal';
import { FieldType } from '@devsnc/sn-glide-form-controls';
import {
	escapeSeismicSpecialSymbols,
	executeEffectIfValidEventSource
} from '@devsnc/library-form';
import { getElementName } from '../../utils/seismic';
import { QUERY_CHANGED } from '@devsnc/sn-scripting-environment';
import { QueryUtils } from '../../utils/query';
import { getProperty } from 'sn-uxpage-presource';
const NOW_BUTTON_CLICKED = 'NOW_BUTTON#CLICKED';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_DOM_READY } = actionTypes;
const WorkspaceDomainFormLayoutElement = getElementName(
	'domain-form-layout',
	'private'
);
const DOMAIN_CAPTION_SYS_PROP = 'glide.sys.domain.domain_section_caption';
const queryUtils = new QueryUtils();
const DOMAIN_NEXT_BUTTON_SOURCE = 'DOMAIN_NEXT_BUTTON_SOURCE';

const escapeValue = fields => {
	return fields.map(f => {
		return {
			...f,
			value: escapeSeismicSpecialSymbols(f.value),
			displayValue: escapeSeismicSpecialSymbols(f.displayValue)
		};
	});
};

const createCallBack = (fields, updateState) => (
	e,
	name,
	value,
	displayValue
) => {
	let changed = false;
	const newFields = fields.map(field => {
		if (field.name === name && field.displayValue !== displayValue) {
			changed = true;
			switch (field.type) {
				case FieldType.GLIDE_LIST:
					return {
						...field,
						value,
						displayValue: displayValue.join(', '),
						display_value_list: displayValue
					};
				default:
					return {
						...field,
						value,
						displayValue
					};
			}
		}
		return field;
	});
	if (changed) {
		updateState({
			fields: newFields
		});
	}
};

const focusFirstInputElement = host => {
	setTimeout(() => {
		const fieldSet = host.shadowRoot.querySelector(
			'sn-form-internal-field-set'
		);
		if (!fieldSet) return;
		const formControl = fieldSet.shadowRoot.querySelector(
			'sn-record-input-connected,sn-record-reference-connected'
		);
		if (!formControl) return;
		const inputWrapper = formControl.shadowRoot.querySelector(
			'.sn-control'
		);
		const input = formControl.shadowRoot.querySelector('input');
		if (!input || !inputWrapper) return;
		input.focus();
		inputWrapper.classList.add('is-focus');
	}, 400);
};

createCustomElement(WorkspaceDomainFormLayoutElement, {
	renderer: {
		type: snabbdom,
		view(state, { updateState }) {
			const {
				componentId,
				properties: { fields: propFields, title, tableDisplayValue },
				fields = propFields
			} = state;
			const caption = t(
				getProperty(DOMAIN_CAPTION_SYS_PROP, 'Domain') || 'Domain'
			);
			const buttonLabel = t('Next');
			return (
				<Fragment>
					<div className="sn-domain-form-header-component now-p-inline--lg">
						<div className="truncate">
							<h1>
								{title
									? title
									: t('Create New {0}', tableDisplayValue)}
							</h1>
						</div>
						<now-button
							config-aria={{ 'aria-label': buttonLabel }}
							label={buttonLabel}
							size="md"
							tooltip-content={t(
								'Continue to create new {0}',
								tableDisplayValue
							)}
							variant="primary"
							disabled={
								fields
									.filter(f => f.mandatory)
									.every(f => f.displayValue)
									? 'false'
									: 'true'
							}
							append-to-payload={{
								eventSource: DOMAIN_NEXT_BUTTON_SOURCE
							}}
						/>
					</div>
					<section
						className="sn-workspace-form-layout-contents"
						tabIndex="-1"
					>
						<div className="sn-form-column-layout-container ws-form">
							<div className="sn-form-column-layout ws-form">
								<div className="sn-form-column-layout-col">
									<div className="sn-form-column-layout-dropdown" />
									<div className="sn-form-column-layout-sections">
										<section className="sn-section ws-form">
											<header
												className="sn-section-header"
												tabindex="-1"
											>
												<div className="sn-section-header-content">
													<span>{caption}</span>
												</div>
											</header>
											<div className="sn-section-body">
												<div className="sn-section-form-row">
													<div className="sn-section-form-column">
														<sn-form-internal-field-set
															componentId={`${componentId}_field_set`}
															fields={escapeValue(
																fields
															)}
															onValueChange={createCallBack(
																[...fields],
																updateState
															)}
														/>
													</div>
												</div>
											</div>
										</section>
									</div>
								</div>
							</div>
						</div>
					</section>
				</Fragment>
			);
		}
	},
	properties: {
		title: {},
		fields: {},
		tableDisplayValue: {},
		query: {}
	},
	styles,
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: {
			effect: ({ state: { properties }, updateState }) => {
				updateState({
					fields: properties.fields,
					shouldRender: false
				});
			}
		},
		[COMPONENT_DOM_READY]: ({ host }) => {
			focusFirstInputElement(host);
		},
		[NOW_BUTTON_CLICKED]: {
			...executeEffectIfValidEventSource(DOMAIN_NEXT_BUTTON_SOURCE, {
				effect: ({
					state: {
						properties: { query },
						fields
					},
					dispatch
				}) => {
					if (!fields || !fields.length || !fields[0].displayValue)
						return;
					const newQuery = queryUtils.mergeFormFieldsIntoExistingQuery(
						fields,
						query
					);
					dispatch(QUERY_CHANGED, { query: newQuery });
				}
			}),
			stopPropagation: true
		}
	}
});

export default WorkspaceDomainFormLayoutElement;
