import { createElement } from '../utils/seismic';
import { nowLinkInterceptorBehavior } from '@servicenow/now-activity-stream';
import styles from './AlertList.scss';
import { default as AlertList } from './AlertList';
import { createActionToStateHandler } from '@devsnc/library-form';

const getHtmlArrayFromProps = props => {
	const { notifications = [] } = props;
	return notifications
		.filter(n => (n.message || '') !== '')
		.map(n => n.message);
};

export default createElement(
	{
		name: 'AlertList',
		component: AlertList,
		actionHandlers: {
			'NOW_ALERT#ACTION_CLICKED': {
				stopPropagation: true,
				effect: createActionToStateHandler('dismissClicked')
			}
		},
		behaviors: [
			{
				behavior: nowLinkInterceptorBehavior,
				options: {
					propsToHtmlArray: getHtmlArrayFromProps
				}
			}
		],
		styles
	},
	'private'
);
