import { createCustomElement } from '@servicenow/ui-core';
import {types as metrics} from '@servicenow/ui-metrics';
import { createClientScriptBehavior } from '@devsnc/sn-scripting-environment';
import styles from './formDataConnected.scss';
import { default as WorkspaceDomainFormLayoutElement } from '../workspace/domainLayout/workspaceDomainFormLayout';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import {
	setUserPreference,
	userPreferencesBehavior
} from 'sn-user-preferences';
import {
	getReadOnlyPreviewOverlayProps,
	AttachmentOverlayElement,
	attachmentPreviewActions,
	isAttachmentInSidebar
} from '@servicenow/now-record-common';

import { isAttachmentInRelatedItems } from '../workspace/formTabUtils';
import { t } from 'sn-translate';
import '@servicenow/now-icon';
import { SnAppModalContainerElement } from '@devsnc/sn-scripted-modal';
import _ from 'lodash';

export const FORM_DATA_CONNECTED_ACTIONS = {
	FETCH_STATUS_UPDATED: 'SN_FORM_DATA_CONNECTED#FETCH_STATUS_UPDATED'
};

const renderOverlay = (state, properties) => {
	const { fields, formData = {} } = state;
	if (_.isEmpty(formData)) return;

	const {
		currentUser,
		formActions: { sidebarActions, relatedItems } = {},
		isReadOnly,
		layout: { isNewRecord, sysId },
		record: { query, view, table }
	} = formData;

	const formModel = {
		table,
		sysId,
		query,
		view,
		currentUser,
		isNewRecord,
		isReadOnly,
		fields
	};
	const isAttachmentAvailable =
		isAttachmentInSidebar(formModel, sidebarActions) ||
		isAttachmentInRelatedItems(formModel, relatedItems);
	if (isAttachmentAvailable) return null;
	const props = getReadOnlyPreviewOverlayProps(state, properties);
	return <AttachmentOverlayElement {...props} />;
};

export const view = state => {
	const {
		formModal,
		formData,
		componentId,
		properties: { table, sysId, query }
	} = state;

	const setModalContainerRefIfNeeded = (node, newNode) => {
		const ref = newNode || node;
		if (ref && formModal) {
			formModal.setContainer(ref.elm, componentId + Date.now());
		}
	};

	const renderFormData = () => {
		return (
			<Fragment>
				{renderOverlay(state, state.properties)}
				<div
					id="form-data-connected"
					hook={{
						update: setModalContainerRefIfNeeded,
						insert: setModalContainerRefIfNeeded
					}}
				/>
				<slot />
			</Fragment>
		);
	};

	const domainLayoutProps = formData &&
		formData.uiDomain && {
			...formData.uiDomain,
			componentId: `${componentId}_uidomain`,
			query,
			table,
			sysId
		};

	return (
		<Fragment>
			<SnAppModalContainerElement />
			{domainLayoutProps ? (
				<WorkspaceDomainFormLayoutElement {...domainLayoutProps} />
			) : (
				renderFormData()
			)}
		</Fragment>
	);
};

const components = [
	'now-record-common-header',
	'now-record-common-sidebar',
	'now-record-common-uiactionbar',
	'now-record-form-blob',
	'now-activity-stream-compose-connected',
	'now-activity-stream-connected',
	'sn-record-ribbon',
	'now-record-common-attachments-connected',
	'now-record-form-section-column-layout'
];
const name = 'sn-form-data-connected';
const clientScriptBehavior = createClientScriptBehavior(components, {
	publicActions: FORM_DATA_CONNECTED_ACTIONS
});

const formLayoutActionHandlers = {
	'RECORD_DETAILS_TAB#PANE_DISPLAYED#CHANGED': {
		effect: ({ action, dispatch, properties, updateProperties }) => {
			const layoutTypeMap = {
				left: 0,
				both: 1,
				right: 2
			};
			const { displayedPane } = action.payload;

			if (!displayedPane)
				return;

			const selectedLayoutType = layoutTypeMap[displayedPane];
			const userPreferenceName = `workspace.layout.type.${properties.table}`;

			const pathToLayoutType = 'nowRecordFormBlob.currentUser.formLayoutConfig.formLayoutType';
			const currentLayoutType = _.get(properties, pathToLayoutType);

			if (selectedLayoutType !== currentLayoutType) {
				setUserPreference(
					userPreferenceName,
					selectedLayoutType,
					dispatch
				);
				updateProperties(() => ({
					path: pathToLayoutType,
					value: selectedLayoutType,
					operation: 'set'
				}));
			}
		},
		stopPropagation: true
	},
	'RECORD_DETAILS_TAB#PANE_SIZES#CHANGED': {
		effect: ({
			action: { payload },
			dispatch,
			properties,
			updateProperties
		}) => {
			const { dividerPosition: formSizeRatio } = payload;
			const userPreferenceName = `workspace.layout.form_ratio.${properties.table}`;

			if (!formSizeRatio)
				return;

			const pathToSizeRatio = 'nowRecordFormBlob.currentUser.formLayoutConfig.formSizeRatio';
			const currentSizeRatio = _.get(properties, pathToSizeRatio);

			if (formSizeRatio !== currentSizeRatio) {
				setUserPreference(userPreferenceName, formSizeRatio, dispatch);
				updateProperties(() => ({
					path: pathToSizeRatio,
					value: formSizeRatio,
					operation: 'set'
				}));
			}
		},
		stopPropagation: true
	}
};

createCustomElement(name, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{ behavior: preWsConfigSysId },
		{ behavior: clientScriptBehavior },
		{ behavior: userPreferencesBehavior }
	],
	dispatches: {
		/**
		 * Loading status
		 *
		 * @property {string} status - {('started'|'succeeded'|'failed')}
		 * @property {string} time
		 */
		[FORM_DATA_CONNECTED_ACTIONS.FETCH_STATUS_UPDATED]: {}
	},
	properties: {
		detailsContext: {},
		nowMetricTypes: {
			default: [metrics.RENDER_TREE, metrics.GQL, metrics.HERO_INTERACTIVE]
		},
	},
	actionHandlers: {
		...attachmentPreviewActions.actionHandlers,
		...formLayoutActionHandlers
	},
	initialState: {
		notifications: []
	},
	styles
});

export default name;
