import { Component, PropTypes } from '../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import { default as WorkspaceFormLayoutElement } from './WorkspaceFormLayoutElement';
import { default as WorkspaceQuickFormLayoutElement } from './quickLayout/WorkspaceQuickFormLayout';
import { default as WorkspaceDomainFormLayoutElement } from './domainLayout/workspaceDomainFormLayout';
import packageJson from '../../../../package.json';
import { ATFHelper } from '@devsnc/library-form';
import {
	getConfig,
	getGFormAttributes
} from '@devsnc/sn-scripting-environment';
import _ from 'lodash';
import { SnAppModalContainer } from '@devsnc/sn-scripted-modal';

const getChildComponentId = baseId => {
	return `${baseId}_formlayout`;
};

class WorkspaceForm extends Component {
	constructor(props) {
		super(props);
		this.onAction = this.onAction.bind(this);
		this.atfHelper = ATFHelper(window);
	}

	componentWillMount() {
		if (!window.formComponentRegistry) {
			const formDependencies = [
				'library-form',
				'sn-scripted-modal',
				'sn-scripting-environment',
				'now-record-common'
			];
			const dependencies = _.get(packageJson, 'dependencies', {});
			const registry = Object.keys(dependencies).reduce((reg, key) => {
				const match = formDependencies.some(dep => key.includes(dep));
				if (match) reg[key] = dependencies[key];
				return reg;
			}, {});

			window.formComponentRegistry = registry;
		}

		this.componentWillReceiveProps(this.props);
	}

	componentWillUnmount() {
		if (window.formComponentRegistry) {
			delete window.formComponentRegistry;
		}
	}

	componentWillReceiveProps(changedProps) {
		if (this.liveUpdate) {
			this.liveUpdate.applyPropData(this.props, changedProps);
		}
	}

	onAction(action, payload) {
		this.props.dispatch(action, payload);
	}

	render() {
		const {
			componentId,
			properties: {
				active,
				dehydrate,
				readOnlyForm,
				hideDetails = false,
				defaultTab,
				formLayoutElement = WorkspaceFormLayoutElement,
				detailsContext,
				selectedTabIndex,
				query: chromeQuery,
				scriptingConfig
			},
			formData,
			fields,
			sections,
			relatedLists,
			glideEnvironment,
			formModal,
			notifications = [],
			isDirty,
			loading,
			isReloading,
			selectedTabName,
			templateRefresh
		} = this.props;
		const childComponentId = getChildComponentId(componentId);

		const quickFormClass =
			formLayoutElement === WorkspaceQuickFormLayoutElement
				? 'quick-edit-form'
				: '';

		if (formData && formData.uiDomain) {
			const layoutProps = {
				...formData.uiDomain,
				componentId: childComponentId,
				query: chromeQuery
			};
			return (
				<div className={`sn-workspace-form ${quickFormClass}`}>
					<WorkspaceDomainFormLayoutElement {...layoutProps} />
				</div>
			);
		}

		const LayoutElement = formLayoutElement;

		//TODO: rename isReloading to something else, since reloading is actually a flag to not re-render the whole form and only rerender form detail section
		if (!formData || !formData.record || (loading && !isReloading)) {
			// loading!
			return (
				<LayoutElement
					component-id={childComponentId}
					is-loading-record={true}
				/>
			);
		}
		const { table, view, query } = formData.record;

		const gForm = glideEnvironment ? glideEnvironment.gForm : null;
		const gUIActions = glideEnvironment
			? glideEnvironment.gUIActions
			: null;
		const { sysId, formSettings, formTabs } = formData.layout;
		const defaultTabOverride =
			defaultTab || _.get(formTabs, 'defaultTabFocus');
		const hideDetailsOverride =
			hideDetails || _.get(formSettings, 'isDetailsHidden');
		const isSectionCollapseDisabled = _.get(
			formSettings,
			'isSectionCollapseDisabled'
		);
		const gFormAttributes = getGFormAttributes(
			this.props,
			this.props.properties
		);

		// TODO: change how this works to avoid JSON blob being passed around
		const config = getConfig(formData, true);
		this.atfHelper.initATFGForm(
			gForm,
			gUIActions,
			glideEnvironment.closeFormAction
		);

		const {
			activityStream,
			ribbon,
			formHeader,
			specialHandlingNote = {},
			currentUser: userDataSource,
			formActions,
			recordTags,
			workspaceConfigId,
			attachments,
			uiActions: { actionNodes, fixedButtonNumber },
			forcedViewName
		} = formData;

		const handleUIActionClick = !_.isEmpty(gUIActions)
			? action => gUIActions.click(action.id)
			: undefined;
		const currentUser = _.omit(userDataSource, ['formLayoutConfig']);
		const formLayoutConfig = userDataSource.formLayoutConfig || {};

		return (
			<div className={`sn-workspace-form ${quickFormClass}`}>
				<SnAppModalContainer />
				<LayoutElement
					component-id={childComponentId}
					is-loading-record={false}
					table={table}
					sys-id={sysId}
					view={view}
					forced-view-name={forcedViewName}
					query={query}
					active={active}
					dehydrate={dehydrate}
					sections={sections}
					fields={fields}
					handle-ui-action-click={handleUIActionClick}
					action-nodes={actionNodes}
					fixed-button-number={fixedButtonNumber}
					related-lists={relatedLists}
					related-items={formActions.relatedItems}
					sidebar-actions={formActions.sidebarActions}
					recordTags={recordTags.recordTags}
					attachments={attachments}
					form-modal={formModal}
					form-header={formHeader}
					read-only-form={readOnlyForm}
					hide-details={hideDetailsOverride}
					disable-collapse={isSectionCollapseDisabled}
					default-tab={defaultTabOverride}
					form-layout-config={formLayoutConfig}
					ribbon={ribbon}
					special-handling-note={specialHandlingNote}
					activity-stream={activityStream}
					config={config}
					current-user={currentUser}
					is-dirty={isDirty}
					workspace-config-id={workspaceConfigId}
					notifications={notifications}
					form-tabs={formTabs}
					selected-tab-index={_.parseInt(selectedTabIndex)}
					selected-tab-name={selectedTabName}
					is-reloading={isReloading}
					classic-form={true}
					templateRefresh={templateRefresh}
					{...gFormAttributes}
					{...scriptingConfig || {}}
				/>
			</div>
		);
	}
}

WorkspaceForm.propTypes = {
	table: PropTypes.string,
	sysId: PropTypes.string,
	view: PropTypes.string,
	views: PropTypes.any,
	forcedViewName: PropTypes.string,
	query: PropTypes.string,
	active: PropTypes.bool,
	dehydrate: PropTypes.bool,
	readOnlyForm: PropTypes.bool,
	hideDetails: PropTypes.bool,
	defaultTab: PropTypes.string,
	formLayoutElement: PropTypes.string,
	workspaceConfigId: PropTypes.string,
	detailsContext: PropTypes.array,
	selectedTabIndex: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	scriptingConfig: PropTypes.object,
	nowDsDomainScope: PropTypes.string,
	nowDsDomainId: PropTypes.string,
	nowDsRecordTable: PropTypes.string,
	nowDsRecordId: PropTypes.string
};

export default WorkspaceForm;
