import {
	React,
	Component,
	PropTypes,
	propsToAttributes,
	getInstanceId
} from '../../utils/seismic';
import { default as RibbonVisibilityController } from './RibbonVisibilityController';
import {
	HeaderElement,
	UIActionBarElement,
	RibbonElement,
	UI_ACTION_BAR_DEFAULT_WIDTH_PERCENTAGE
} from '@servicenow/now-record-common';

import react from '@servicenow/ui-renderer-react';
import _ from 'lodash';

class HeaderLayout extends Component {
	constructor(props) {
		super(props);
		const { showRibbon, ribbon } = props.properties;

		this.ribbonVisibility = new RibbonVisibilityController(
			ribbon,
			showRibbon
		);

		this.uiActionBarWidthPercentage =
			props.uiActionWidthPercentage ||
			UI_ACTION_BAR_DEFAULT_WIDTH_PERCENTAGE;

		this.refLayoutRow = this.refLayoutRow.bind(this);
		this.state = {};
	}

	componentWillReceiveProps(changedProps) {
		const { showRibbon, ribbon } = changedProps.properties;
		const { isRibbonVisible } = changedProps;
		const {
			behaviors: {
				resize: {
					size: { width }
				}
			}
		} = changedProps;
		const prevWidth = this.state.width;

		if (ribbon && ribbon !== this.props.properties.ribbon) {
			this.ribbonVisibility.ribbon = ribbon;
		}
		if (showRibbon !== this.props.properties.showRibbon) {
			this.ribbonVisibility.setPreference(showRibbon);
		}

		if (this.props.isRibbonVisible !== isRibbonVisible) {
			this.ribbonVisibility.toggle();
		}

		if (!prevWidth || prevWidth !== width) {
			const layoutRow = this.state.layoutRow;
			const uiActionBarWidth =
				layoutRow &&
				layoutRow.getBoundingClientRect().width *
					(this.uiActionBarWidthPercentage / 100);
			this.setState({ width, uiActionBarWidth });
		}
	}

	get baseInstanceId() {
		const componentId = getInstanceId(this.props);
		return componentId.split('_form')[0];
	}

	refLayoutRow(node) {
		if (node && !this.state.layoutRow) {
			this.setState({
				layoutRow: node
			});
		}
	}

	render() {
		const {
			table,
			sysId,
			actionNodes,
			fixedButtonNumber,
			handleClick,
			recordTags,
			ribbon,
			formHeader,
			isReadonly,
			hideUiActions,
			active,
			classicForm,
			overlayContainer
		} = this.props.properties;
		const recordDisplayValue = formHeader.recordDisplayValue;
		const { canRenderRibbon, isVisible } = this.ribbonVisibility;

		const componentId = getInstanceId(this.props);
		const ribbonContainerId = `${componentId}_ribbon_container`;
		const headerAttributes = propsToAttributes({
			...formHeader,
			table,
			recordTags,
			sysId,
			recordDisplayValue,
			classicForm,
			overlayContainer
		});
		const uiActionBarAttributes = propsToAttributes({
			componentId: `${componentId}_uiActions`,
			actionNodes,
			fixedButtonNumber,
			handleClick,
			width: this.state.uiActionBarWidth,
			classicForm
		});
		const ribbonAttributes = propsToAttributes({
			componentId: `${componentId}_ribbon`,
			widgets: ribbon.widgets,
			table,
			sysId,
			active,
			isVisible: canRenderRibbon,
			collapsed: !isVisible,
			classicForm
		});

		return (
			<header className="sn-header-layout">
				<div className="sn-header-layout-row" ref={this.refLayoutRow}>
					<div className="sn-header-layout-content">
						<HeaderElement {...headerAttributes} />
					</div>
					{!hideUiActions && (
						<div className="sn-header-layout-content -last">
							{!isReadonly && (
								<UIActionBarElement
									{...uiActionBarAttributes}
								/>
							)}
						</div>
					)}
				</div>
				{canRenderRibbon && !_.isEmpty(ribbon.widgets) && (
					<div
						className="sn-header-layout-ribbon"
						id={ribbonContainerId}
					>
						<RibbonElement {...ribbonAttributes} />
					</div>
				)}
			</header>
		);
	}
}

HeaderLayout.propTypes = {
	formHeader: PropTypes.object,
	isReadonly: PropTypes.bool,
	hideUiActions: PropTypes.bool,
	sysId: PropTypes.string,
	table: PropTypes.string,
	actionNodes: PropTypes.array,
	fixedButtonNumber: PropTypes.number,
	handleClick: PropTypes.func,
	ribbon: PropTypes.object,
	showRibbon: PropTypes.bool,
	labelId: PropTypes.string,
	active: PropTypes.bool,
	recordTags: PropTypes.object,
	classicForm: PropTypes.bool,
	overlayContainer: PropTypes.string,
};

export default HeaderLayout;
