import { PureComponent, PropTypes } from '../../utils/seismic';
import react from '@servicenow/ui-renderer-react';
/*
To implement Toggle declare the following:

this.toggleSection = this.toggleSection.bind(this);
this.state = {
    isVisible: true
}

toggleSection() {
    this.setState({
        isVisible: !this.state.isVisible
    });
}

Then wrap the HTML you want expanded/collapsed between:
<Toggle isVisible={this.state.isVisible} >
. . .
</Toggle>

If you want re-useable HTML wrapped the call:
<Toggle isVisible={this.state.isVisible} children={this.props.children} />

*/

class Toggle extends PureComponent {
	getToggleStyle() {
		return this.props.isVisible ? {} : { display: 'none' };
	}

	render() {
		return (
			<div className="sn-toggle-wrapper" style={this.getToggleStyle()}>
				{this.props.children}
			</div>
		);
	}
}

Toggle.propTypes = {
	isVisible: PropTypes.bool,
	children: PropTypes.object
};

export default Toggle;
