import { t } from 'sn-translate';

const WORKSPACE_LAYOUT = 'workspace.layout';
export const WORKSPACE_LAYOUT_PREFERENCES = {
	LAYOUT_TYPE: `${WORKSPACE_LAYOUT}.type`,
	FORM_RATIO: `${WORKSPACE_LAYOUT}.form_ratio`,
	SIDEBAR_RATIO: `${WORKSPACE_LAYOUT}.sidebar_ratio`
};

export const layoutConfigKeys = {
	layoutType: 'LAYOUT_TYPE',
	formRatio: 'FORM_RATIO',
	sidebarRatio: 'SIDEBAR_RATIO'
};

export const getUserPreferenceName = (key, tableName) => {
	return `${WORKSPACE_LAYOUT_PREFERENCES[key]}.${tableName}`;
};

export const LAYOUT_TYPE = {
	FORM: 0,
	HALF: 1,
	ACTIVITY: 2
};

const LAYOUT_TYPE_BUTTON = {
	[LAYOUT_TYPE.FORM]: {
		label: t('Show form in full screen'),
		type: 'recordDetailsOutline',
		id: `[suffix]-toolbar-button-${LAYOUT_TYPE.FORM}`
	},
	[LAYOUT_TYPE.HALF]: {
		label: t('Show form and activity'),
		type: 'recordActivityStreamOutline',
		id: `[suffix]-toolbar-button-${LAYOUT_TYPE.HALF}`
	},
	[LAYOUT_TYPE.ACTIVITY]: {
		label: t('Show activity in full screen'),
		type: 'recordActivityOutline',
		id: `[suffix]-toolbar-button-${LAYOUT_TYPE.ACTIVITY}`
	}
};

const BUTTONS_ORDER = [
	LAYOUT_TYPE.FORM,
	LAYOUT_TYPE.HALF,
	LAYOUT_TYPE.ACTIVITY
];
const TOOLBAR_BUTTONS = BUTTONS_ORDER.map(
	layoutType => LAYOUT_TYPE_BUTTON[layoutType]
);

export const getLayoutButtons = instanceId => {
	return TOOLBAR_BUTTONS.map(button => ({
		...button,
		id: button.id.replace('[suffix]', instanceId)
	}));
};

export const getLayoutType = button => {
	if (!button) return LAYOUT_TYPE.HALF;
	const searchId = button.id.substr(-1);
	const index = TOOLBAR_BUTTONS.findIndex(
		btn => btn.id.substr(-1) === searchId
	);
	return BUTTONS_ORDER[index];
};

export const getSelectedLayoutButtonIndex = layoutType => {
	return BUTTONS_ORDER.findIndex(type => type === layoutType);
};
