import { createElement } from '../utils/seismic';
import { default as WorkspaceForm } from './WorkspaceForm';
import styles from './WorkspaceForm.scss';
import {
	createClientScriptBehavior,
	notificationActions
} from '@devsnc/sn-scripting-environment';
import { actionTypes } from '@servicenow/ui-core';
import {types as metrics} from '@servicenow/ui-metrics';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import { FORM_SIDEBAR_RESIZED } from './WorkspaceFormConstants';
import {
	FORM_ACTIVITY_STREAM_LAYOUT_RESIZED,
	FORM_ACTIVITY_LAYOUT_TYPE_CHANGED
} from '../../components/form/layout/columnLayoutActions';
import {
	setUserPreference,
	userPreferencesBehavior
} from 'sn-user-preferences';
import {
	getUserPreferenceName,
	layoutConfigKeys
} from '../form/layout/formLayoutHelper';
const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

const clientScriptBehavior = createClientScriptBehavior(null, {
	classicForm: true
});

export default createElement(
	{
		name: 'FormConnected',
		component: WorkspaceForm,
		behaviors: [
			{ behavior: preWsConfigSysId },
			{ behavior: clientScriptBehavior },
			{ behavior: userPreferencesBehavior }
		],
		initialState: {
			classicForm: true
		},
		styles,
		actionHandlers: {
			[COMPONENT_PROPERTY_CHANGED]: {
				effect({ action, updateState }) {
					const {
						payload: { name, value, previousValue }
					} = action;
					const detailsContext = value;
					if (value === previousValue) return;
					if (name === 'query') {
						updateState({
							domainQuery: null,
							domainQuerySet: false,
							shouldRender: false
						});
						return;
					}
					if (name !== 'detailsContext') return;
					const filterNotViewed = detailsContext.filter(detail => {
						return detail.op !== 'viewed';
					});

					if (!filterNotViewed.length) return;

					const shouldRefreshTemplate = filterNotViewed.some(
						detailContext =>
							detailContext.table === 'sys_template'
					);

					updateState({
						templateRefresh: shouldRefreshTemplate ? Date.now() : null
					});
				}
			},
			[FORM_ACTIVITY_STREAM_LAYOUT_RESIZED]: {
				effect: ({ action: { payload }, dispatch, updateState }) => {
					const { table, formSizeRatio } = payload;
					const userPreferenceName = getUserPreferenceName(
						layoutConfigKeys.formRatio,
						table
					);
					setUserPreference(
						userPreferenceName,
						formSizeRatio,
						dispatch
					);
					updateState({
						path:
							'formData.currentUser.formLayoutConfig.formSizeRatio',
						value: formSizeRatio,
						operation: 'set'
					});
				},
				stopPropagation: true
			},
			[FORM_ACTIVITY_LAYOUT_TYPE_CHANGED]: {
				effect: ({ action: { payload }, dispatch, updateState }) => {
					const { table, selectedLayoutType } = payload;
					const userPreferenceName = getUserPreferenceName(
						layoutConfigKeys.layoutType,
						table
					);
					setUserPreference(
						userPreferenceName,
						selectedLayoutType,
						dispatch
					);
					updateState({
						path:
							'formData.currentUser.formLayoutConfig.formLayoutType',
						value: selectedLayoutType,
						operation: 'set'
					});
				},
				stopPropagation: true
			},
			[FORM_SIDEBAR_RESIZED]: {
				effect: ({ action: { payload }, dispatch, updateState }) => {
					const { table, sideBarSizeRatio } = payload;
					const userPreferenceName = getUserPreferenceName(
						layoutConfigKeys.sidebarRatio,
						table
					);
					setUserPreference(
						userPreferenceName,
						sideBarSizeRatio,
						dispatch
					);
					updateState({
						path:
							'formData.currentUser.formLayoutConfig.sideBarSizeRatio',
						value: sideBarSizeRatio,
						operation: 'set'
					});
				},
				stopPropagation: true
			}
		},
		properties: {
			table: {},
			sysId: {},
			view: {},
			views: {},
			forcedViewName: {},
			query: {},
			active: {},
			dehydrate: {},
			readOnlyForm: {},
			hideDetails: {},
			defaultTab: {},
			formLayoutElement: {},
			workspaceConfigId: {},
			detailsContext: {},
			selectedTabIndex: {},
			scriptingConfig: {},
			nowDsDomainScope: {},
			nowDsDomainId: {},
			nowDsRecordTable: {},
			nowDsRecordId: {},
			nowMetricTypes: {
				default: [metrics.RENDER_TREE, metrics.GQL, metrics.HERO_INTERACTIVE]
			},
		},
	},
	'public'
);
