import React from 'react';
import _ from 'lodash';

// external dependencies
const SN_COMPONENT_LIST = 'now-record-list-connected-related';
const SN_COMPONENT_VEDITOR = 'sn-catalog-form';
const SN_COMPONENT_ACTIVITY_COMBO = 'now-activity-combo';
const SN_COMPONENT_SHN_MODAL = 'sn-component-shn-modal';

// form control dependencies
import { FieldType } from '@devsnc/sn-glide-form-controls';

export const FORM_CONTROL_TYPES = Object.values(FieldType);
let FORM_CONTROL_STATE = {};
FORM_CONTROL_TYPES.map(type => {
	FORM_CONTROL_STATE[type] = true;
});

export const ELEMENT_TAGS = {
	SN_COMPONENT_LIST,
	SN_COMPONENT_VEDITOR,
	SN_COMPONENT_ACTIVITY_COMBO,
	SN_COMPONENT_SHN_MODAL
};

let ELEMENT_TAG_STATE = {
	[SN_COMPONENT_LIST]: true,
	[SN_COMPONENT_VEDITOR]: true,
	[SN_COMPONENT_ACTIVITY_COMBO]: true,
	[SN_COMPONENT_SHN_MODAL]: true
};

export function getElementState(elementTag) {
	return ELEMENT_TAG_STATE[elementTag];
}

export function setElementState(elementTag, state) {
	const currentState = ELEMENT_TAG_STATE[elementTag];
	if (_.isUndefined(currentState)) {
		console.warn('Unregistered element tag: ', elementTag);
	}
	ELEMENT_TAG_STATE[elementTag] = state ? true : false;
}

function getDisabledElement(elementTag, isConfigured) {
	const message = isConfigured
		? `${elementTag} is not enabled!`
		: `${elementTag} is not configured. Double check the tag name.`;

	// using a HOC pattern
	return class extends React.Component {
		render() {
			return <div key={elementTag}>{message}</div>;
		}
	};
}

export function getElementTag(elementTag) {
	const isEnabled = ELEMENT_TAG_STATE[elementTag];
	if (isEnabled) {
		return elementTag;
	}
	return getDisabledElement(elementTag, typeof isEnabled !== 'undefined');
}

export function getFormControlState(fieldType) {
	const isEnabled = FORM_CONTROL_STATE[fieldType];
	return typeof isEnabled === 'undefined' ? true : isEnabled;
}

export function setFormControlState(fieldType, state) {
	const currentState = FORM_CONTROL_STATE[fieldType];
	if (_.isUndefined(currentState)) {
		console.warn('Unregistered control type: ', fieldType);
	}
	FORM_CONTROL_STATE[fieldType] = state ? true : false;
}
