const tempSeparator = '$$TEMPSEPARATOR$$';
const termpSeparator = '^';

const replaceLiteralTermSeparator = value =>
	value.replace(new RegExp('\\^{2,}', 'g'), function(match) {
		return tempSeparator.repeat(match.length);
	});

const replaceTempSeparator = value =>
	value.replace(/\$\$TEMPSEPARATOR\$\$/g, termpSeparator);

export function QueryUtils() {
	return {
		mergeFormFieldsIntoExistingQuery: (fields, query) => {
			if (!fields) return query;
			const nvs = fields
				.filter(f => f.name && f.value !== undefined)
				.map(f => ({
					keyValuePair: `${f.name}=${f.value || ''}`,
					hasValue: !!f.value
				}));
			if (!nvs.length) return query;
			let newQuery = '';
			// if there is an existing query, merge form fields values into the query
			// want to overwrite any query values with values that exist on form, else leave in place
			if (query) {
				// conditional builder separates terms with '^', but these could
				// be present in a text value, when they do they are escaped as ^^, handle this
				newQuery = replaceLiteralTermSeparator(query);
				nvs.forEach(nv => {
					const parts = nv.keyValuePair.split('=');
					const value = parts[1];
					// if the value is the same, do nothing
					if (newQuery.indexOf(nv.keyValuePair) > -1) return;
					// if the form field key exists in the querystring and is different, find the place in queryString and inject in newValue
					// since it does exist in the initial query, we need to allow deleting the value, so still replace even if newValue is empty
					if (
						newQuery.indexOf(parts[0]) === 0 ||
						newQuery.indexOf(termpSeparator + parts[0]) > -1
					) {
						const pattern = `(${parts[0]}=)(.*?)(\\^{1})`;
						const regex = new RegExp(pattern, 'g');
						newQuery = newQuery.replace(
							regex,
							value ? '$1' + value + termpSeparator : ''
						);
					} else if (nv.hasValue)
						// else just put the newValue at the end
						newQuery +=
							(newQuery[newQuery.length - 1] !== termpSeparator
								? termpSeparator
								: '') + nv.keyValuePair;
				});
				newQuery = replaceTempSeparator(newQuery);
			} //if no query just construct from existing values
			else
				newQuery = nvs
					.filter(nvs => nvs.hasValue)
					.map(nvs => nvs.keyValuePair)
					.join('^');
			return newQuery;
		}
	};
}
