// 'transparent' in IE and Firefox, and 'rgba(0, 0, 0, 0)' in Webkit browsers
export const isTransparent = color => color === 'transparent' || color === 'rgba(0, 0, 0, 0)';

// input: 'rgb(255, 255, 0)'; output: #ffff00
export const rgb2hex = str => "#"+str.split(',').map(s => (s.replace(/\D/g,'')|0).toString(16)).map(s => s.length < 2 ? "0"+s : s).join('');

// input: 'rgb(255, 255, 0)'; 
// output: {
//   "red": "211",
//   "green": "211",
//   "blue": "211"
// }
export const getRGB = str => {
    var match = str.match(/rgba?\((\d{1,3}), ?(\d{1,3}), ?(\d{1,3})\)?(?:, ?(\d(?:\.\d?))\))?/);
    return match ? {
      red: match[1],
      green: match[2],
      blue: match[3]
    } : {};
  }