import {
	SIMPLE_FORM_HEADER_CLICK_ACTION,
	SIMPLE_FORM_FOOTER_CLICK_ACTION,
	MULTI_FORM_ACTIONS,
	FORM_SAVE_STATUS
} from '../../../constants/actions';
import { multiRecordUpdateHttpEffect } from '../../../datasources/httpEffects/update';
import { MULTI_UPDATE_INTERNAL_ACTIONS } from '../../../datasources/httpEffects/updateActionsConst';
import { MULTI_FORM_INTERNAL_ACTIONS } from './workspaceMultiFormActionsConst';
import { createEffectsInterceptors } from '@devsnc/library-form';

const handleSaveEvent = (
	{ sysIds, changedFields: updates, table },
	dispatch
) => {
	dispatch(MULTI_FORM_INTERNAL_ACTIONS.SAVE, {
		data: { sys_ids: sysIds.split(','), updates },
		table
	});
};

const handleFormClose = (updateState, dispatch, isDirty) => {
	!isDirty
		? dispatch(MULTI_FORM_ACTIONS.CLOSE)
		: updateState({
				displayModal: true
		  });
};

const getChangedFields = fields => {
	return Object.entries(fields)
		.filter(([key, val]) => val.value !== '' && !!val.isDirty)
		.reduce((acc, [key, val]) => {
			return {
				...acc,
				[val.name]: val.value
			};
		}, {});
};

export default {
	actionHandlers: {
		[SIMPLE_FORM_HEADER_CLICK_ACTION]: {
			effect: ({ state: { isDirty }, dispatch, updateState }) => {
				handleFormClose(updateState, dispatch, isDirty);
			},
			stopPropagation: true
		},
		[SIMPLE_FORM_FOOTER_CLICK_ACTION]: {
			effect: ({
				state: {
					properties: { sysIds, table },
					changedFields,
					isDirty
				},
				updateState,
				dispatch,
				action: { payload }
			}) => {
				if (payload.action === 'cancel')
					handleFormClose(updateState, dispatch, isDirty);
				else if (payload.action === 'save') {
					handleSaveEvent(
						{
							sysIds,
							table,
							changedFields
						},
						dispatch
					);
				}
			},
			stopPropagation: true
		},
		[MULTI_UPDATE_INTERNAL_ACTIONS.SAVE_SUCCEEDED]: {
			effect: ({ action: { payload }, dispatch }) => {
				const records = _.get(payload, 'result.records', []).map(
					record => {
						return {
							sysId: record.sysId,
							displayValue: record.displayValue,
							status: record.status,
							...(record.updated && {
								updated: record.updated
							}),
							...(record.errors && { errors: record.errors })
						};
					}
				);

				const result = {
					errorCount: _.get(payload, 'result.errorCount'),
					recordCount: _.get(payload, 'result.recordCount'),
					records
				};

				dispatch(MULTI_FORM_ACTIONS.SAVE_SUCCEEDED, { result });
			},
			stopPropagation: true
		},
		[MULTI_UPDATE_INTERNAL_ACTIONS.SAVE_FAILED]: {
			effect: ({ action: { payload }, dispatch }) => {
				const error = _.get(payload, 'error', {});
				dispatch(MULTI_FORM_ACTIONS.SAVE_FAILED, {
					error: {
						detail: error.detail,
						message: error.message
					},
					status: FORM_SAVE_STATUS.FAILURE
				});
			},
			stopPropagation: true
		},
		[MULTI_FORM_INTERNAL_ACTIONS.SAVE]: {
			...multiRecordUpdateHttpEffect,
			stopPropagation: true
		},
		[MULTI_FORM_INTERNAL_ACTIONS.CHANGE]: {
			...createEffectsInterceptors([
				({ updateState, action: { payload }, state }) => {
					const { changedFields } = state;
					const { fields } = payload;
					const newChangedFields = getChangedFields(fields);
					if (!_.isEqual(changedFields, newChangedFields)){
						updateState({
							isDirty: !!Object.keys(newChangedFields).length,
							changedFields: newChangedFields
						});
						dispatch(MULTI_FORM_ACTIONS.DIRTY_CHANGED, {
							isDirty: payload.isDirty
						});
					}
				}
			]),
			stopPropagation: true
		},
		[MULTI_FORM_INTERNAL_ACTIONS.LOADING]: {
			effect: ({ updateState, action: { payload } }) => {
				updateState({ loading: payload.loading });
			},
			stopPropagation: true
		},
		[MULTI_FORM_INTERNAL_ACTIONS.DISCARD_MODAL]: {
			...createEffectsInterceptors([
				({ updateState }) => {
					updateState({ displayModal: false });
				},
				({ dispatch }) => {
					dispatch(MULTI_FORM_ACTIONS.CLOSE);
				}
			]),
			stopPropagation: true
		},
		'NOW_MODAL#OPENED_SET': {
			effect: ({ updateState }) => {
				updateState({ displayModal: false });
			},
			stopPropagation: true
		}
	}
};

