import {
	React,
	PureComponent,
	PropTypes,
	propsToAttributes
} from '../../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import _ from 'lodash';
import {
	AttachmentElement,
	RelatedItemElement
} from '@servicenow/now-record-common';

const TAB_CLASS_NAME = 'sn-tab-item';
const TAB_HIDDEN_CLASS_NAME = 'is-hidden';

class TabContents extends PureComponent {
	constructor(props) {
		super(props);

		// apply the initial state
		let activated = {};
		if (props) {
			const { selectedTabIndex } = props.properties;
			if (!_.isUndefined(selectedTabIndex)) {
				activated[selectedTabIndex] = true;
			}
		}
		this.state = {
			activated
		};
	}

	componentWillReceiveProps(changedProps) {
		const { selectedTabIndex } = changedProps.properties;
		if (selectedTabIndex !== this.props.properties.selectedTabIndex) {
			const activated = Object.assign({}, this.state.activated, {
				[selectedTabIndex]: true
			});
			this.setState({
				activated
			});
		}
	}

	isSelected(index) {
		return this.props.properties.selectedTabIndex === index;
	}

	isActivated(index) {
		return this.state.activated[index] === true;
	}

	getContentClassName(index) {
		return (
			TAB_CLASS_NAME +
			(this.isSelected(index) ? '' : ' ' + TAB_HIDDEN_CLASS_NAME)
		);
	}

	activateAttachmentTabIfPresent(contents) {
		const attachmentIndex = contents.findIndex(
			content =>
				content.component === RelatedItemElement &&
				content.props.componentName === AttachmentElement
		);

		if (attachmentIndex > -1 && !this.isActivated(attachmentIndex)) {
			const activated = {
				...this.state.activated,
				[attachmentIndex]: true
			};
			this.setState({
				activated
			});
		}
	}

	render() {
		const {
			contents,
			tabPanelIds,
			tabContentExtras = {}
		} = this.props.properties;

		if (!contents) {
			return null;
		}

		if (!_.isEmpty(tabContentExtras)) {
			this.activateAttachmentTabIfPresent(contents);
		}

		return (
			<ul className="sn-tab-items" role="none">
				{contents.map((content, index) => {
					return this.renderTabContents(
						index,
						content,
						tabPanelIds[index],
						tabContentExtras
					);
				})}
			</ul>
		);
	}

	renderTabContents(index, content, tabPanelId, tabContentExtras) {
		if (!this.isActivated(index)) {
			return null;
		}

		const { style, isSeismic, component, props } = content;
		const extraContent = !_.isEmpty(tabContentExtras)
			? tabContentExtras[component] ||
			  tabContentExtras[props.componentName] ||
			  {}
			: {};

		const componentProps = isSeismic
			? propsToAttributes(props)
			: { ...props };
		const Component = component;

		return (
			<li
				key={props.componentId || index}
				style={style}
				className={this.getContentClassName(index)}
				role="tabpanel"
				id={tabPanelId}
			>
				<Component
					component-id={componentProps.componentId}
					tab-content-extras={extraContent}
					{...componentProps}
				/>
			</li>
		);
	}
}

TabContents.propTypes = {
	contents: PropTypes.array,
	selectedTabIndex: PropTypes.number,
	tabPanelIds: PropTypes.array,
	selectedTab: PropTypes.string,
	tabContentExtras: PropTypes.object,
	formLayoutConfig: PropTypes.object
};

export default TabContents;
