import { view } from './formBlob';
import { createCustomElement } from '@servicenow/ui-core';
import styles from './formBlob.scss';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import formBlobActions from './formBlobActions';
import {
	userPreferencesBehavior,
	getUserPreference
} from 'sn-user-preferences';

const WRAP_LIST_TEXT = 'workspace.wrapListText';
const ROWCOUNT = 'rowcount';

const FormBlobElement = 'now-record-form-blob';
/**
 * Displays the main details (fields, related items, related lists) of the given record for the given table
 * based on the configured workspace and view/view rules
 *
 * ```jsx
 * <now-record-form-blob
 *		active={...}
 *		activityStream={...}
 *		config={...}
 *		currentUser={...}
 *		defaultTab={...}
 *		fields={...}
 *		formModal={...}
 *		formTabs={...}
 *		hasErrorMessage={...}
 *		hideDetails={...}
 *		query={...}
 *		relatedItems={...}
 *		relatedLists={...}
 *		sections={...}
 *		selectedTabIndex={...}
 *		sysId={...}
 *		table={...}
 *		view={...}
 *		workspaceConfigId={...}
 * 		isReloading={...}
 * 		isAttachmentUploadDisabled={...}
 * 		serializedChanges={...}
 * 		encodedRecord={...}
 *		/>
 * ```
 *
 * @seismicElement now-record-form-blob
 * @summary Displays the main details (fields, related items, related lists) of the given record for the given table
 * based on the configured workspace and view/view rules
 */
createCustomElement(FormBlobElement, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [userPreferencesBehavior],
	properties: {
		/**
		 * table of the record being displayed
		 *
		 * @type {string}
		 */
		table: {},
		/**
		 * id of the record being displayed
		 * @type {string}
		 */
		sysId: {},
		/**
		 * specific view of the tables configured layout to render
		 * @type {string}
		 */
		view: {},
		/**
		 * @type {string}
		 */
		query: {},
		/**
		 * id of the workspace the record is being loaded in eg. (agent, hr, vlm)
		 * @type {string}
		 */
		workspaceConfigId: {},
		/**
		 * array of field objects, each object contains all the info to be passed to the field component which will be rendered
		 * @type {Array.<object>}
		 */
		fields: {},
		/**
		 * array of section object, each object contains all the info about each section eg (id, order, rows, fields in each row)
		 * @type {Array.<object>}
		 */
		sections: {},
		/**
		 * default tab to select after the control is rendered
		 * @type {string}
		 */
		defaultTab: {},
		/**
		 * has config related values for the record
		 * @type {FormBlobConfig}
		 */
		config: { default: {} },
		/**
		 * Object containing information on form, activity stream, sidebar layout ratios.
		 * @type {object}
		 */
		formLayoutConfig: {},
		/**
		 * object representing the different modals that have been registered and can be displayed (eg. alert, confirm, confirmDestroy, custom modals)
		 * @type {object}
		 */
		formModal: {},
		/**
		 * array of objects containing the properties for rendering each relatedItem and its component in a tab
		 * @type {Array.<object>}
		 */
		relatedItems: {},
		/**
		 * object which gets data about current user
		 * @type {Object}
		 */
		currentUser: {},
		/**
		 * whether or not to hide the details tab
		 * @type {boolean}
		 */
		hideDetails: {},
		/**
		 * whether or not there is an error message to display
		 * @type {boolean}
		 */
		hasErrorMessage: { default: false },
		/**
		 * object containing information on customized tab ordering configuration
		 * @type {object}
		 */
		formTabs: {},
		/**
		 * array of objects containing the properties for rendering each related list component in a tab
		 * @type {Array.<object>}
		 */
		relatedLists: {},
		/**
		 * currently selected tab index, default to 0
		 * @type {number}
		 */
		selectedTabIndex: {},
		/**
		 * object containing properties for rendering the activity stream component
		 * @type {Object}
		 */
		activityStream: {},
		/**
		 * whether or not the form is active
		 * @type {boolean}
		 */
		active: {},
		/**
		 * computed field
		 * @type {boolean}
		 */
		wrapListText: {
			computed: state =>
				getUserPreference(WRAP_LIST_TEXT, state, 'false') === 'true'
		},
		/**
		 * computed field
		 * @type {boolean}
		 */
		relatedListLimit: {
			computed: state => getUserPreference(ROWCOUNT, state, 20)
		},
		/**
		 * whether or not the form is in reloading state
		 * @type {boolean}
		 */
		isReloading: {},
		/**
		 * whether or not attachment upload is disabled from gform
		 * @type {boolean}
		 */
		isAttachmentUploadDisabled: {},
		/**
		 * serialized dirty fields
		 * @type {string}
		 */
		serializedChanges: {},
		/**
		 * encoded record
		 * @type {string}
		 */
		encodedRecord: {},
		/**
		 * whether or not this is rendered in the workspace Form
		 * @type {boolean}
		 * @private
		 */
		classicForm: {},
		/**
		 * tab name to be selected
		 * @type {string}
		 * @private
		 */
		selectedTabName: {},
		/**
		 * object whose properties will be spread and added to the components being rendered as additional attributes
		 * @type {TabContentExtra}
		 */
		tabContentExtras: {
			default: {}
		}
	},
	styles,
	...formBlobActions,
	dispatches: {
		/**
		 * Dispatched when a new tab is clicked
		 *
		 * @type {ContentUpdatedPayload}
		 */
		CONTENT_UPDATED: {}
	}
});

export default FormBlobElement;
