import { PureComponent, PropTypes } from '../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import _ from 'lodash';
import {
	CLEAR_NOTIFICATIONS,
	REMOVE_NOTIFICATION
} from '@devsnc/sn-scripting-environment';
import '@servicenow/now-alert';
import { default as AlertListElement } from '../alerts/AlertListElement';

const NOTIFICATION_SORT_ORDER = {
	critical: 0,
	warning: 1,
	info: 2,
	positive: 3
};

const hasHtml = message => {
	const node = document.createElement('div');
	node.innerHTML = message;
	return (
		Array.from(node.childNodes).findIndex(child => child.nodeType === 1) !==
		-1
	);
};

const getStatus = type => {
	switch (type) {
		case 'info':
			return 'info';
		case 'error':
			return 'critical';
		case 'warning':
			return 'warning';
		case 'success':
			return 'positive';
		default:
			return 'info';
	}
};

const getIcon = type => {
	switch (type) {
		case 'error':
			return 'circle-close-outline';
		case 'warning':
			return 'exclamation-circle-outline';
		case 'success':
			return 'check-circle-outline';
		default:
			return 'info-circle-outline';
	}
};

const transform = (notifications, idPrefix = '') => {
	const action = {
		type: 'dismiss'
	};

	const transformedNotifications = notifications.map(
		(notification, index) => {
			const { type, message } = notification;
			const isHtml = hasHtml(message);
			const content = {
				type: isHtml ? 'html' : 'string',
				value: isHtml ? `<div>${message}</div>` : message
			};

			return {
				id: `${idPrefix}${index}`,
				originalIndex: index,
				status: getStatus(type),
				icon: getIcon(type),
				content,
				action
			};
		}
	);

	return transformedNotifications.sort(
		(a, b) =>
			NOTIFICATION_SORT_ORDER[a.status] -
			NOTIFICATION_SORT_ORDER[b.status]
	);
};

const getAlertIdPrefix = parentId => `${parentId}-alert-`;

class NotificationsWrapper extends PureComponent {
	constructor(props) {
		super(props);
		this.dismissNotification = this.dismissNotification.bind(this);
		this.dismissAllNotifications = this.dismissAllNotifications.bind(this);
		this.state = {
			items: transform(
				_.get(props, `properties.notifications`, []),
				getAlertIdPrefix(props.componentId)
			)
		};
	}

	componentWillReceiveProps(changedProps) {
		const newNotifications = _.get(
			changedProps,
			'properties.notifications',
			[]
		);
		const oldNotifications = _.get(
			this.props,
			'properties.notifications',
			[]
		);
		if (newNotifications !== oldNotifications) {
			const items = transform(
				newNotifications,
				getAlertIdPrefix(this.props.componentId)
			);
			this.setState({ items });
		} else if (
			changedProps.alertItemsChanged !== this.props.alertItemsChanged
		) {
			const { value = [] } = changedProps.alertItemsChanged;
			const {
				properties: { notifications, table, sysId }
			} = this.props;

			if (!value.length) {
				this.props.dispatch(CLEAR_NOTIFICATIONS, {
					notificationAction: {
						type: 'clear'
					}
				});
			} else {
				const deletedItem = _.first(
					_.difference(this.state.items, value)
				);
				if (!deletedItem) return;
				const index = parseInt(_.last(deletedItem.id.split('-')), 10);
				if (index < 0 || index > notifications.length) return;
				this.props.dispatch(REMOVE_NOTIFICATION, {
					index,
					notification: notifications[index],
					sysId,
					table
				});
			}
		}
	}

	dismissNotification(index) {
		const {
			properties: { notifications, table, sysId }
		} = this.props;
		const notification = notifications[index];
		this.props.dispatch(REMOVE_NOTIFICATION, {
			index,
			notification,
			sysId,
			table
		});
	}

	dismissAllNotifications() {
		const { table, sysId } = this.props.properties;
		this.props.dispatch(CLEAR_NOTIFICATIONS, {
			notificationAction: {
				type: 'clear'
			}
		});
	}

	render() {
		const { items } = this.state || [];
		if (!items || items.length == 0) {
			return null;
		}

		return (
			<AlertListElement
				notifications={items}
				onDismiss={this.dismissNotification}
				onDismissAll={this.dismissAllNotifications}
			/>
		);
	}
}

NotificationsWrapper.propTypes = {
	table: PropTypes.string,
	sysId: PropTypes.string,
	notifications: PropTypes.array
};

export default NotificationsWrapper;
