import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '../WorkspaceFormElement';
import WorkspaceQuickFormLayoutElement from './WorkspaceQuickFormLayout';
import styles from './WorkspaceQuickForm.scss';
import {
	EXTERNAL_ACTIONS,
	ADD_NOTIFICATIONS
} from '@devsnc/sn-scripting-environment';
import { default as trapActions } from '../common/actions/trapActionsFormEmbeddedMode';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import _ from 'lodash';
import '@servicenow/now-modal';
import { QUICK_MODAL_PROPS } from './WorkspaceQuickFormConstants';
import { QUICK_FORM_ACTIONS } from '../../../constants/actions';

const WorkspaceQuickFormElement = 'sn-record-common-quick-form';

/**
 * ```jsx
 * <sn-record-common-quick-form
 *		componentId={...}
 *		sysId={...}
 *		table={...}
 *		view={...}
 *		workspaceConfigId={...}
 *		/>
 * ```
 *
 * @seismicElement sn-record-common-quick-form
 * @summary This component offers a paired down version of the normal workspace form (now-record-form-blob).\nIt displays and allows editing for the primary form fields based on the view and has another tab\nfor the activity stream. It does not contain any related lists\nor UiActionBar, agent assist, sidebar, etc.
 */
createCustomElement(WorkspaceQuickFormElement, {
	renderer: {
		type: snabbdom,
		view(state) {
			const wsConfigSysId = _.get(state, 'behaviors.wsConfigSysId', '');

			const {
				properties: {
					table,
					sysId,
					view,
					workspaceConfigId = wsConfigSysId
				},
				componentId
			} = state;

			return (
				<div className="quick-form now-p-block--lg">
					<now-record-form-connected
						table={table}
						sys-id={sysId}
						view={view}
						workspace-config-id={workspaceConfigId}
						component-id={`${componentId}_quickform`}
						form-layout-element={WorkspaceQuickFormLayoutElement}
						scripting-config={{
							proxyNotifications: true
						}}
					/>
				</div>
			);
		}
	},
	behaviors: [{ behavior: preWsConfigSysId }],
	properties: {
		/**
		 * table of the record being displayed
		 *
		 * @type {string}
		 */
		table: {},
		/**
		 * id of the record being displayed
		 * @type {string}
		 */
		sysId: {},
		/**
		 * specific view of the tables configured layout to render
		 * @type {string}
		 */
		view: {},
		/**
		 * can define the id of the component-id attribute, if not will be auto generated
		 * @type {string}
		 */
		componentId: {},
		/**
		 * id of the workspace the record is being loaded in eg. (agent, hr, vlm)
		 * @type {string}
		 */
		workspaceConfigId: {
			default: '7b24ceae5304130084acddeeff7b12a3' // default value to again workspace to workaround issue of quickform inside list to fallback to use the legacy agentworkspace field decorators.
		}
	},
	actionHandlers: {
		[EXTERNAL_ACTIONS.SEND_FORM_NOTIFICATION]: {
			effect: ({ action, dispatch }) => {
				dispatch(ADD_NOTIFICATIONS, action.payload);
			},
			stopPropagation: false
		},
		...trapActions.actionHandlers
	},
	dispatches: {
		/**
		 * Dispatched when a notification is generated from this component or any of its child components
		 *
		 * @type {AddNotificationsPayload}
		 */
		ADD_NOTIFICATIONS: {},
		/**
		 * Dispatched when a user has closed the quick form by clicking on the 'X' button in the top right
		 */
		[QUICK_FORM_ACTIONS.CLOSE]: {},
		/**
		 * Dispatched after a user has clicked save and the save operation has succeeded
		 *
		 * @type {QuickFormSaveActionPayload}
		 */
		[QUICK_FORM_ACTIONS.SAVE_SUCCEEDED]: {},
		/**
		 * Dispatched after a user has clicked save and the save operation has failed
		 *
		 * @type {QuickFormSaveActionPayload}
		 */
		[QUICK_FORM_ACTIONS.SAVE_FAILED]: {},
		/**
		 * Dispatched when the dirty state of the form has changed, true for dirty, false for not dirty
		 *
		 * @type {boolean}
		 */
		[QUICK_FORM_ACTIONS.DIRTY_CHANGED]: {}
	},
	styles
});
export default WorkspaceQuickFormElement;
