import { createCustomElement } from '@servicenow/ui-core';
import react from '@servicenow/ui-renderer-react';
import { default as SeismicReact } from 'react';
import _ from 'lodash';
import { default as Types } from 'prop-types';
export const PropTypes = Types;
export const React = SeismicReact;
export const Component = SeismicReact.Component;
export const PureComponent = SeismicReact.PureComponent;

export function getPrivateElementName(componentName) {
	return 'sn-form-internal-' + _.kebabCase(componentName);
}

export function getPublicElementName(componentName) {
	return 'now-record-' + _.kebabCase(componentName);
}

export function getProtectedElementName(componentName) {
	return 'sn-' + _.kebabCase(componentName);
}

export const getElementName = (name, prefixModifier) => {
	switch (prefixModifier) {
		case 'public':
			return getPublicElementName(name);
		case 'protected':
			return getProtectedElementName(name);
		case 'private':
		default:
			return getPrivateElementName(name);
	}
};

function getProperties(propTypes, defaultProps) {
	if (!propTypes) return {};

	return Object.keys(propTypes).reduce((acc, name) => {
		const defaultValue =
			defaultProps && defaultProps[name]
				? { default: defaultProps[name] }
				: {};
		return {
			...acc,
			[_.camelCase(name)]: defaultValue
		};
	}, {});
}

const transformProps = component => {
	const { propTypes, defaultProps } = component;
	if (propTypes) {
		component.propTypes = {
			properties: PropTypes.shape({
				...propTypes
			})
		};
	}

	if (defaultProps) {
		component.defaultProps = {
			properties: {
				...defaultProps
			}
		};
	}

	return { propTypes, defaultProps };
};
/**
 * Creates a seismic element and returns the tag name
 * @param options
 */
export function createElement(options, prefixModifier) {
	const {
		name,
		component,
		actions,
		actionHandlers,
		behaviors,
		initialState,
		styles,
		properties,
		onPropertiesSet,
		onBootstrap,
		onConnect,
		onDisconnect
	} = options;

	const { propTypes, defaultProps } = transformProps(component);

	let extras = {};
	if (onPropertiesSet) extras = { ...extras, onPropertiesSet };
	if (onBootstrap) extras = { ...extras, onBootstrap };
	if (onConnect) extras = { ...extras, onConnect };
	if (onDisconnect) extras = { ...extras, onDisconnect };

	const elementName = getElementName(name, prefixModifier);

	createCustomElement(elementName, {
		renderer: {
			type: react,
			view: component
		},
		properties: properties || getProperties(propTypes, defaultProps),
		actions,
		actionHandlers,
		behaviors,
		initialState,
		styles,
		...extras
	});
	return elementName;
}

/**
 * Reads the incoming componentId for namespacing children
 * @param props
 * @return {*}
 */
export function getInstanceId(props) {
	let { instanceId } = props;
	if (_.isUndefined(instanceId)) {
		instanceId = props['component-id'] || props['componentId'];
	}
	return instanceId;
}

/**
 * Return the base instance id
 * @param instanceId
 * @return {*}
 */
export function getBaseInstanceId(instanceId) {
	return instanceId.split('_form')[0];
}

/**
 * Converts prop to attributes
 * @param componentId
 * @param props
 */
export function propsToAttributes(props) {
	let attr = {};
	Object.keys(props).map(name => {
		attr[_.kebabCase(name)] = props[name];
	});
	return attr;
}
