import { createElement } from '../utils/seismic';
import { default as NotificationsWrapper } from './NotificationsWrapper';
import { nowLinkInterceptorBehavior } from '@servicenow/now-activity-stream';

const getHtmlArrayFromProps = props => {
	const { notifications = [] } = props;
	return notifications
		.filter(n => (n.message || '') !== '')
		.map(n => n.message);
};

export default createElement(
	{
		name: 'NotificationsWrapper',
		component: NotificationsWrapper,
		behaviors: [
			{
				behavior: nowLinkInterceptorBehavior,
				options: {
					propsToHtmlArray: getHtmlArrayFromProps
				}
			}
		]
	},
	'private'
);
