import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom, {
	Fragment,
	createRef
} from '@servicenow/ui-renderer-snabbdom';
import {types as metrics} from '@servicenow/ui-metrics';
import { default as ColumnLayoutElement } from '../../form/layout/ColumnLayoutElement';
import _ from 'lodash';
import { createClientScriptBehavior ,
	getConfig,
	getGFormAttributes
} from '@devsnc/sn-scripting-environment';

import styles from './formSectionConnected.scss';
import { default as NotificationsWrapperElement } from '../../../components/notifications/NotificationsWrapperElement';
import { getFetchActions } from '@devsnc/library-form';

const fieldPropName = "fields"
const FORM_SECTION_CONNECTED_COMPONENT = 'now-record-form-section-connected';
const { COMPONENT_DOM_TREE_READY } = actionTypes;

const clientScriptBehavior = createClientScriptBehavior(null, {
	fetchFormDataActions: getFetchActions(FORM_SECTION_CONNECTED_COMPONENT),
	classicForm: true
});

const settingProps = (props, ref, updateState, key, host, e) => {
	if (!e.currentTarget || !e.currentTarget.assignedNodes) return;

	const nodes = e.currentTarget.assignedNodes();
	const node = _.first(nodes);
	// makes sure slotted node is seismic element by checking version matches with current component's seismic version
	if (!node || node.version !== host.version) return;

	_.each(props, (value, name) => {
		if (value !== undefined)  
			node[name] = value;
	});

	// When slot node changes what is passed in as ref and node will be different thus reference needs to be updated
	if (!ref || ref.current !== node) {
		const newRef = createRef();
		newRef.current = node;
		updateState({
			[key]: newRef,
			shouldRender: false
		});
	}

	return node;
};

const updateSubcomponentsFields = (fields, state) => {
	const { headerRef, footerRef } = state;

	if (headerRef && headerRef.current && fields) {
		if (!_.isEqual(headerRef.current[fieldPropName], fields))
			headerRef.current[fieldPropName] = fields;
	}

	if (footerRef && footerRef.current && fields) {
		if (!_.isEqual(footerRef.current[fieldPropName], fields))
			footerRef.current[fieldPropName] = fields;
	}
};

// Create view for renderer
const view = (state, { updateState }) => {
	const {
		componentId,
		formData,
		fields,
		sections,
		glideEnvironment,
		formModal,
		notifications,
		headerRef,
		footerRef,
		host,
		properties: {
			inUiBuilder
		}
	} = state;

	if (!formData || !formData.record) {
		// loading!
		return null;
	}

	const {
		layout: { sysId },
		record: { table, view, forcedViewName, query }
	} = formData;

	// TODO: change how this works to avoid JSON blob being passed around
	const config = {
		...getConfig(formData),
		showActivityStream: false,
		isSectionMenuHidden: true
	};
	const attrs = {
		componentId: `${componentId}_column_layout`,
		table,
		sysId,
		query,
		fields,
		sections,
		config,
		formModal,
		view,
		forcedViewName,
		scrollableHost: host,
		classicForm: true,
		inUiBuilder
	};

	const {
		table: attrTable,
		sysId: attrSysId,
		view: attrView,
		forcedViewName: attrForcedViewName,
		query: attrQuery
	} = state.properties;

	const setHeaderProps = _.partial(
		settingProps,
		{ fields },
		headerRef,
		updateState,
		'headerRef',
		host
	);
	const setFooterProps = _.partial(
		settingProps,
		{ fields },
		footerRef,
		updateState,
		'footerRef',
		host
	);
	updateSubcomponentsFields(fields, state);

	const gFormAttributes = getGFormAttributes(state, state.properties);

	return (
		<div className="now-record-form-section-connected">
			<NotificationsWrapperElement
				table={table}
				notifications={notifications}
			/>
			<slot name="header" on-slotchange={setHeaderProps} />
			<ColumnLayoutElement {...attrs} {...gFormAttributes} />
			<slot name="footer" on-slotchange={setFooterProps} />
		</div>
	);
};

/**
 * Displays the fields and values of a given record and table.
 * Form layout is based on the configured workspace, view, and view rules.
 *
 * ```jsx
 * <now-record-form-section-connected
 *		table={...}
 *		sysId={...}
 *		view={...}
 *		forcedViewName={...}
 *		query={...}
 *		readOnlyForm={...}
 *		/>
 * ```
 *
 * @seismicElement now-record-form-section-connected
 * @summary Displays a record in a table.
 */
createCustomElement(FORM_SECTION_CONNECTED_COMPONENT, {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [{ behavior: clientScriptBehavior }],
	initialState: {
		classicForm: true
	},
	properties: {
		/**
		 * Table of the record to display.
		 *
		 * @type {string}
		 */
		table: {},
		/**
		 * Sys ID of the record to display.
		 * @type {string}
		 */
		sysId: {},
		/**
		 * View of the table's configured layout to render.
		 * @type {string}
		 */
		view: {},
		/**
		 * When true, overrides the view.
		 * @type {string}
		 */
		forcedViewName: {},
		/**
		 * Encoded query string used to filter the fields displayed.
		 * @type {string}
		 */
		query: {},
		/**
		 * When true, the form is read only.
		 * @type {boolean}
		 */
		readOnlyForm: {
			default: false,
			unstableParse: true
		},
		/**
		 * When true, the form is composed inside a UI Builder page.
		 * @type {boolean}
		 * @private
		 */
		inUiBuilder: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		nowMetricTypes: {
			default: [metrics.RENDER_TREE, metrics.GQL, metrics.HERO_INTERACTIVE]
		},
		/**
		 * ID of the workspace to load the record in. For example, agent, hr, or vlm.
		 * By default, the record loads in agent workspace.
		 * @type {string}
		 */
		workspaceConfigId: {
			default: '7b24ceae5304130084acddeeff7b12a3' // default value to again workspace to workaround issue of quickform inside list to fallback to use the legacy agentworkspace field decorators.
		}
	},
	actionHandlers: {
		[COMPONENT_DOM_TREE_READY]: {
			effect({
				updateState,
				action: {
					payload: { host }
				}
			}) {
				updateState({
					host,
					shouldRender: false
				});
			}
		}
	},
	styles
});

export default FORM_SECTION_CONNECTED_COMPONENT;
