import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
import { RelatedItemElement } from '@servicenow/now-record-common';
import { transformPreviewRecordPayload, createReDispatchActionHandler } from '@devsnc/library-form';
import {
	getWrappedActionPayload,
	transformNotificationPayload
} from '../../utils/effect';
const RELATED_ITEMS_TAB_CONTENT = 'now-record-related-items-tab-content';

function guid() {
	const x = 2147483648;
	return (
		Math.floor(Math.random() * x).toString(36) +
		Math.abs(Math.floor(Math.random() * x) ^ Date.now()).toString(36)
	);
}

const getRelatedItemsComponentId = componentIdPrefix =>
	`${componentIdPrefix}_related_items_${guid()}`;

const getRelatedItemProps = properties => {
	const { actionComponent, formModel, actionAttributes } = properties;
	return {
		componentName: actionComponent,
		actionAttributes,
		formModel
	};
};

const view = state => {
	const { componentId } = state;

	const props = getRelatedItemProps(state.properties);
	const relatedItemComponentId = getRelatedItemsComponentId(componentId);
	return (
		<RelatedItemElement component-id={relatedItemComponentId} {...props} />
	);
};

createCustomElement(RELATED_ITEMS_TAB_CONTENT, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * the tag of the component to be rendered when clicking on this action - eg 'now-agent-assist'
		 */
		actionComponent: { default: {}, schema: { type: 'string' } },
		/**
		 * this is an object which contains the data needed for all of the components represented by all of the actions to load
		 * @type {FormModel}
		 */
		formModel: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		 *  * @property {object} actionAttributes - object attributes to send down to the actionComponent - eg '{"thing1":"hello","thing2":"world"}'
		 */
		actionAttributes: { default: {}, schema: { type: 'object' } }
	},
	actionHandlers: {
		PREVIEW_RECORD: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#PREVIEW_RECORD',
			transformPreviewRecordPayload
		),
		ITEM_SELECTED: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#ITEM_SELECTED'
		),
		ADD_NOTIFICATIONS: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#ADD_NOTIFICATIONS',
			transformNotificationPayload
		),
		REMOVE_NOTIFICATIONS: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#REMOVE_NOTIFICATIONS',
			transformNotificationPayload
		),
		CLEAR_NOTIFICATIONS: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#CLEAR_NOTIFICATIONS'
		),
		WRAPPED_ACTION: {
			effect: ({ dispatch, action }) => {
				dispatch(
					'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#WRAPPED_ACTION',
					getWrappedActionPayload(action)
				);
			},
			stopPropagation: true
		},
		SCREEN_STATUS_CHANGED: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#SCREEN_STATUS_CHANGED'
		),
		EXECUTE_UI_ACTION: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#EXECUTE_UI_ACTION'
		),
		REFRESH_REQUESTED: createReDispatchActionHandler(
			'NOW_RECORD_RELATED_ITEMS_TAB_CONTENT#REFRESH_REQUESTED'
		)
	}
});

export default RELATED_ITEMS_TAB_CONTENT;
