import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import styles from './SimpleFormHeader.scss';
import { default as actionHandlers } from './SimpleFormHeaderActionHandler';
import { t } from 'sn-translate';

const SimpleFormHeaderElement = 'sn-form-internal-simple-form-header';

createCustomElement(SimpleFormHeaderElement, {
	renderer: {
		type: snabbdom,
		view(state) {
			const {
				properties: { title, tooltip, ariaLabelClose }
			} = state;
			return (
				<Fragment>
					<div className="sn-simple-form-header-component now-p-inline--lg">
						<div className="truncate">
							<h1>{title}</h1>
						</div>
						<now-button
							icon-name="close-outline"
							size="md"
							variant="tertiary"
							bare
							tooltip-content={tooltip}
							config-aria={{ 'aria-label': ariaLabelClose }}
						/>
					</div>
				</Fragment>
			);
		}
	},
	properties: {
		title: {
			default: ''
		},
		tooltip: {
			default: t('Close')
		},
		ariaLabelClose: {
			default: t('Close')
		}
	},
	styles,
	actionHandlers
});

export default SimpleFormHeaderElement;
