/**
 * Small utility class to orchestrate the ribbon visibility
 */
class RibbonVisibilityController {
	constructor(ribbon, preference) {
		this._preference = preference;
		this._canRenderRibbon = preference;
		this._visibility = preference;
		this.ribbon = ribbon;
	}

	// when ribbon object is reset, it should reset all state including _visibility. _visibility should set back to use user preferences value , if there is some widgets.
	set ribbon(ribbon = {}) {
		this._ribbon = ribbon;
		this._canRenderRibbon = this.hasRibbonWidgets;
		this._visibility = this.hasRibbonWidgets && this._preference;
	}

	get hasRibbonWidgets() {
		return !!this._ribbon.hasRibbonWidgets;
	}

	get canRenderRibbon() {
		return this._canRenderRibbon;
	}

	setPreference(isVisible) {
		if (this._preference === isVisible && this._visibility === isVisible) {
			return false;
		}

		this._preference = isVisible;
		this._visibility = isVisible;
		return true;
	}

	toggle() {
		this._visibility = !this._visibility;
	}

	/**
	 * Returns the computed visibility
	 * @return {*}
	 */
	get isVisible() {
		return this._visibility;
	}
}

export default RibbonVisibilityController;
