import { SIMPLE_FORM_FOOTER_CLICK_ACTION } from '../../../constants/actions';
import { SIMPLE_FORM_FOOTER_BUTTON_NAME } from './SimpleFormFooterConst';

const NOW_BUTTON_CLICKED = 'NOW_BUTTON#CLICKED';

const handleClick = (action, dispatch) => {
	dispatch(SIMPLE_FORM_FOOTER_CLICK_ACTION, {
		action
	});
};

export default {
	[NOW_BUTTON_CLICKED]: {
		effect: ({
			dispatch,
			action: {
				meta: { componentName }
			},
			state
		}) => {
			if (componentName === SIMPLE_FORM_FOOTER_BUTTON_NAME.CANCEL)
				handleClick('cancel', dispatch);
			else if (componentName === SIMPLE_FORM_FOOTER_BUTTON_NAME.CONFIRM) {
				const {
					properties: { isDirty }
				} = state;
				if (isDirty) handleClick('save', dispatch);
			}
		},
		stopPropagation: true
	}
};
