import _ from 'lodash';
import styles from './WorkspaceSimpleForm.scss';
import { createElement } from '../../../utils/seismic';
import { default as WorkspaceSimpleForm } from './WorkspaceSimpleForm';
import {
	REFERENCE_INFO_CLICK,
	dispatchPreviewRecord,
	createSimpleFormClientScriptBehavior
} from '@devsnc/sn-scripting-environment';

const clientScriptBehavior = createSimpleFormClientScriptBehavior();
const WORKSPACE_SIMPLE_FORM = 'workspace-simple-form';

export default createElement(
	{
		name: WORKSPACE_SIMPLE_FORM,
		component: WorkspaceSimpleForm,
		behaviors: [{ behavior: clientScriptBehavior }],
		actionHandlers: {
			[REFERENCE_INFO_CLICK]: ({ action, dispatch }) => {
				const referenceInfoClick = _.get(
					action,
					'payload.referenceInfoClick',
					{}
				);
				const {
					referencetable: refTable,
					sys_id: refSysId
				} = referenceInfoClick;

				dispatchPreviewRecord(dispatch, {
					table: refTable,
					sysId: refSysId
				});
			}
		},

		styles
	},
	'private'
);
