import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import styles from './SimpleFormFooter.scss';
import { default as actionHandlers } from './SimpleFormFooterActionHandler';
import { SIMPLE_FORM_FOOTER_BUTTON_NAME } from './SimpleFormFooterConst';
import { t } from 'sn-translate';

const SimpleFormFooterElement = 'sn-form-internal-simple-form-footer';

createCustomElement(SimpleFormFooterElement, {
	renderer: {
		type: snabbdom,
		view(state, { dispatch }) {
			const {
				properties: { isDirty, cancelButtonTitle, confirmButtonTitle }
			} = state;

			return (
				<div className="sn-simple-form-footer-component now-align -inline-end now-p-inline--lg">
					<now-button
						className="now-m-inline-end--sm"
						variant="secondary"
						size="md"
						label={cancelButtonTitle}
						component-name={SIMPLE_FORM_FOOTER_BUTTON_NAME.CANCEL}
						tooltip-content={cancelButtonTitle}
					/>
					<now-button
						variant="primary"
						size="md"
						label={confirmButtonTitle}
						disabled={isDirty ? false : 'true'}
						component-name={SIMPLE_FORM_FOOTER_BUTTON_NAME.CONFIRM}
						tooltip-content={confirmButtonTitle}
					/>
				</div>
			);
		}
	},
	properties: {
		isDirty: { default: false },
		cancelButtonTitle: { default: t('Cancel') },
		confirmButtonTitle: { default: t('Save') }
	},
	styles,
	actionHandlers
});

export default SimpleFormFooterElement;
