import {
	PureComponent,
	PropTypes,
	getInstanceId
} from '../../../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import { default as Section } from '../../section/Section';
import renderReadonlyNotification from '../../../common/renderReadOnlyNotification';

const getFieldsForSection = (fields, section) => {
	let sectionFields = {};
	section.rows &&
		section.rows.map(row => {
			row.map(column => {
				column.fields.map(fieldName => {
					sectionFields[fieldName] = { ...fields[fieldName] };
				});
			});
		});
	return sectionFields;
};

class WorkspaceSimpleForm extends PureComponent {
	constructor(props) {
		super(props);
	}

	render() {
		const { formData, fields, gForm, dispatch } = this.props;
		if (!formData || !formData.record) {
			// loading!
			return null;
		}
		const { sections } = formData.simpleFormLayout || {};
		if (!sections || !fields) {
			return null;
		}
		const instanceId = getInstanceId(this.props);
		const serializedChanges =
			gForm && JSON.stringify(gForm.serialize(true));

		return (
			<div>
				<div className="sn-form-column-layout-container">
					<div className="sn-form-column-layout">
						<div className="sn-form-column-layout-col">
							{formData.isMissingWorkspaceView &&
								renderReadonlyNotification()}
							{sections.map((section, index) => {
								const componentId = `${instanceId}_section_${index}`;
								const props = {
									componentId,
									section,
									fields: getFieldsForSection(
										fields,
										section
									),
									serializedChanges,
									isNewRecord: false,
									dispatch
								};
								return (
									<Section
										key={'section-' + index}
										{...props}
										classicForm={true}
									/>
								);
							})}
						</div>
					</div>
				</div>
			</div>
		);
	}
}

WorkspaceSimpleForm.propTypes = {
	table: PropTypes.string,
	view: PropTypes.string,
	views: PropTypes.array,
	sysIds: PropTypes.string,
	workspaceConfigId: PropTypes.string,
	onDataChanged: PropTypes.func,
	onDataLoaded: PropTypes.func
};

export default WorkspaceSimpleForm;
